}

.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 42px;
    background-color: #34495e;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    align-self: auto;
    margin-top: 1px;
    box-shadow: 0px -3px 17px 0px rgba(16, 16, 18, 1);
}

